body {
  color: #060b35;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  display: flex;
}

.header__logo-img {
  height: 40px;
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.navigation__item {
  margin-right: 20px;
}

.navigation__item:nth-last-child(-n+1) {
  margin: 0;
}

.navigation__link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.navigation__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  box-sizing: border-box;
  content: "";
  border: 2px solid #00acdc;
  border-radius: 8px;
  width: 100%;
  display: flex;
  position: absolute;
  bottom: 0;
}

.cards-container {
  grid-gap: 46px 48px;
  grid-template-columns: 200px;
  justify-content: center;
  padding: 50px 40px;
  display: grid;
}

@media screen and (width >= 488px) {
  .cards-container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media screen and (width >= 768px) {
  .cards-container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media screen and (width >= 1024px) {
  .cards-container {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__img {
  align-self: center;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__title {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 150%;
  transition: all .3s;
}

.card:hover .card__title {
  color: #34568b;
}

.card__product-code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  line-height: 140%;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  margin: 0 0 24px;
  display: flex;
}

.card__reviews-count {
  color: #060b35;
  text-align: right;
  margin: 0;
  font-size: 10px;
  line-height: 140%;
}

.card__price {
  justify-content: space-between;
  margin: 0 0 16px;
  display: flex;
}

.card__price-title {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 300;
  line-height: 150%;
}

.card__price-value {
  color: #060b35;
  text-align: right;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 112.5%;
}

.card__buy-btn {
  box-sizing: border-box;
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  padding-block: 11px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

.card__buy-btn:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin: 0;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

